const db = wx.cloud.database()
import {uniIcons} from '@dcloudio/uni-ui'
export default {
	components: {uniIcons},
	data() {
		return {
			list: [],
			currentId: 0
		};
	},
	methods: {
		changePwd(id) {
			this.currentId = id
		},
		
		async sure(id, password) {
			try{
				let res = await db.collection('accounts').doc(id).update({
					data: {
						password
					}
				})
				console.log(res)
				uni.showToast({
					icon: 'none',
					title: '修改改成',
					duration: 1000
				})
				this.currentId = 0
			}catch(e){
				console.log(e)
			}
		},
		
		async getList() {
			try{
				let res = await db.collection('accounts').get()
				console.log(res)
				this.list = res.data
			}catch(e){
				console.log(e)
			}
		}
	},
	onLoad() {
		this.getList()
	}
}
